* {
	margin: 0px;
	border: 0px;
}
.App {
	background-color: aliceblue;
}
.navbar__wrapper {
	margin: auto;
	width: 100%;
	background-color: #30336b;
}
.navbar__container {
	padding: 15px;
	object-fit: contain;
	color: white;
	display: flex;
	justify-content: center;
}
.navbar__header {
	flex: 1;
	text-align: end;
	justify-content: center;
	font-size: 21px;
	align-self: center;
}
.navbar__header h2 {
	display: flex;
	justify-content: flex-end;
	align-self: initial;
}
.navbar__searchbox {
	/* width: 27.33%; */
	position: relative;
	flex: 1;
	padding: 1rem;
}
.navbar__searchbox input[type='text'] {
	width: 100%;
	padding: 10px 10px 10px 10px;
	border-radius: 4px;
}
.navbar__body {
	font-size: x-large;
	margin-left: 15px;
	flex: 1;
	display: flex;
	align-items: center;
}
.cartItem__marker {
	background-color: red;
	width: 15px;
	height: 15px;
	position: absolute;
	top: 0;
	left: 1rem;
	border-radius: 60px;
}
span {
	font-size: initial;
	position: relative;
	top: -0.8rem;
	left: 2px;
}
.navbar__addtoCart,
.navbar__history {
	display: inline-block;
	width: 50%;
	position: relative;
	vertical-align: sub;
	cursor: pointer;
}

p {
	font-size: small !important;
}
.navbar__logout,
.navbar__username {
	cursor: pointer;
	display: inline-block;
	width: 30%;
	vertical-align: text-top;
	font-size: small;
}
.navbar__addtoCart,
.navbar__history {
	display: inline-block;
	width: 50%;
	vertical-align: sub;
	cursor: pointer;
}
.navbar__searchbox svg {
	position: absolute;
	right: 0;
	top: 1.6rem;
	color: black;
	font-size: inherit;
}
.individual__item__wrapper:hover {
	transform: scale(1.03);
	background-color: #ededed;
}
.individual__item__wrapper {
	display: inline-block;
	margin: 1%;
	background-color: white;
	border-radius: 5px;
	border: 1px solid;
	padding: 1%;
	transition: transform 500ms;
	cursor: pointer;
	box-shadow: 3px 5px #a99e9e;
}
.items__container {
	margin: auto;
	max-width: 100%;
	text-align: center;
}
h3.individual__item__name {
	margin: auto;
	font-variant-caps: petite-caps;
	font-size: x-large;
}
.item__addtoCart:hover {
	background-color: #f3cc79;
}
.item__addtoCart {
	border: 1px solid;
	padding: 2%;
	margin: 3%;
	background-color: #fac42f;
	border: none;
	border-radius: 6px;
	padding: 12px;
	font-weight: 700;
	font-size: larger;
}
.cartItem__container {
	min-height: 100vh;
}
img {
	height: 100%;
	width: 100%;
	object-fit: contain;
}
.individual__item__img {
	width: 85%;
	display: contents;
}
.cartItem__body {
	border-bottom: 1px solid;
	object-fit: contain;
	display: flex;
	margin-top: 15px;
	padding-bottom: 10px;
	background-color: white;
	width: 100%;
	box-shadow: 1px 2px 6px 0px #30336b;
}

.cartItem__wrapper {
	margin: auto;
	width: 88%;
}

.cartItem__img {
	display: flex;
	padding: 2%;
	justify-content: center;
	object-fit: contain;
}

.cartItem__details {
	padding-left: 15px;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.cartItem__noItems {
	display: flex;
	margin-bottom: 10px;
	align-items: baseline;
}

.cartItem__name {
	font-size: xx-large;
	margin-bottom: 10px;
	font-variant: petite-caps;
	font-weight: 600;
}

.cartItem__price {
	margin-bottom: 10px;
	font-size: medium;
}
.cartItem__backHome button:hover,
button.cartItem__remove__button:hover {
	box-shadow: inset 1px 1px 20px #9c9277 !important;
}
.cartItem__backHome button,
button.cartItem__remove__button {
	border: none;
	font-weight: 600;
	background-color: #fac42f;
	box-shadow: inset 1px 1px 0px #9c9277;
	font-size: medium;
	padding: 10px;
	border-radius: 6px;
	cursor: pointer;
}
input[type='number'] {
	padding: 10px;
	width: 15%;
	border: 1px solid;
	margin-left: 10px;
	border-radius: 6px;
}
.cartItem__heading {
	padding: 10px;
}
.cartItem__total {
	display: flex;
}
.cartItem__empty {
	width: 50%;
	margin: auto;
	font-size: x-large;
	font-style: italic;
	text-align: center;
	text-decoration: underline;
}
.search__data {
	cursor: pointer;
}

.cartItem__total h2 {
	align-self: center !important;
}
.cartItem__totalAmount {
	margin-left: 5px;
	align-self: center;
}
button.button.cartItem__butButton:hover {
	box-shadow: 0px 0px 10px 0px black;
}
.checkout__container {
	min-height: 85vh;
}
.cartItem__buyButton {
	margin-left: 10px;
}
button.button.cartItem__butButton {
	padding: 8%;
	border: none;
	border-radius: 5px;
	background-color: #8bc34a;
	color: white;
	font-weight: 800;
	cursor: pointer;
}
.cartItem__totol__wrapper {
	flex: 1;
	display: flex;
	justify-content: center;
	align-items: center;
}
.cartItem__heading__warpper {
	/* display: inline-block; */
	flex: 1;
}
.cartItem__container__navbar {
	display: flex;
	margin: auto;
	width: 88%;
}
.checkout__body {
	margin: auto;
	width: 75%;
	display: flex;
	font-size: 25px;
	margin-top: 10px;
}
.checkout__wrapper {
	margin: auto;
	width: 19rem;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	background-color: white;
	margin-top: 20px;
	border: none;
	box-shadow: 1px 1px 3px black;
}
.checkout__heading {
	font-weight: 800;
	font-size: 30px;
	text-align: center;
}
.checkout__itemName {
	flex: 1;
}

.checkount__body__container h6 {
	font-weight: 300;
}

.checkout__totalAmount {
	display: flex;
	justify-content: center;
	margin-top: 15px;
	font-size: larger;
}
.checkout__payNow {
	justify-content: center;
	display: flex;
	padding: 10px;
	margin: 15px;
}

button.button_checkout__goBack {
	padding: 10px;
	margin-right: 10px;
	background-color: orange;
	border-radius: 6px;
	font-size: 20px;
	cursor: pointer;
	transition: 450ms;
}
button.button_checkout__payNow:hover,
button.button_checkout__goBack:hover {
	box-shadow: 1px 0px 7px black;
}
button.button_checkout__payNow {
	padding: 10px;
	margin-right: 10px;
	transition: 450ms;
	background-color: #8bc34a;
	border-radius: 6px;
	font-size: 20px;
	cursor: pointer;
}
.checkout__totalAmount h6 {
	font-size: large;
	text-decoration: underline;
}

.paid__wrapper {
	min-height: 90vh;
}

.paid__heading {
	margin: auto;
	display: flex;
	width: 30%;
	justify-content: center;
	background-color: #8bc34a;
	padding: 10px;
	border-radius: 7px;
	margin-top: 15px;
}

.paid__getBack {
	margin: auto;
	width: 30%;
	display: flex;
	justify-content: center;
	margin-top: 10px;
}

button.button__paid__goBack {
	padding: 10px;
	border-radius: 7px;
	border: none;
	cursor: pointer;
	background-color: orange;
	font-weight: 700;
}
